<template>
  <div id="rooms">
    <mt-header>
      <mt-button
        @click="$emit('cancel')"
        slot="left"
        icon="back"
      >取消</mt-button>
      <mt-button
        @click="_next"
        slot="right"
      >下一步</mt-button>
    </mt-header>
    <div
      class="rooms_box"
      v-if="allConcatRooms"
    >
      <div
        class="choose_room"
        v-for="item in allConcatRooms"
        :key="item.id"
      >
        <p class="room_sn">{{ item.roomSn }}</p>
        <p>&yen;{{ item.roomAmount }}/月</p>
        <p>押金：{{ item.deposit }}</p>
        <img
          class="room_img"
          @click="deleteRoom(item)"
          src="@/assets/five/delete.png"
          alt=""
        >
      </div>
    </div>
    <mt-button
      class="addRooms"
      @click="showRooms = true"
    >✚&ensp;添加房间</mt-button>
    <!-- 选择房间号 -->
    <mt-popup
      v-model="showRooms"
      position="bottom"
      class="chooseRooms"
    >
      <div class="chooseRooms_titleBox">
        <div class="chooseRooms_top">选择房间号</div>
        <div>同价格的房间选在一起设置租金</div>
      </div>
      <div style="height:80%;overflow-y:scroll;">
        <div
          v-for="(room,i) in roomSnListType"
          :key="i"
          class="chooseRooms_box"
          @click="choose_rooms(room)"
        >
          <img
            v-if="room.imgStatu == true"
            src="@/assets/btn_selected.png"
            alt
            class="chooseRooms_img"
          />
          <label
            v-else
            class="chooseRooms_label"
          ></label>
          <p class="chooseRooms_sn">{{  room.roomSn }}</p>
        </div>
      </div>
      <p
        class="chooseRooms_set"
        @click='setDeposit'
      >设置租金</p>
    </mt-popup>
    <!-- 设置押金 -->
    <mt-popup
      v-model="showDeposit"
      position="bottom"
      class="chooseRooms setD"
    >
      <div
        class="chooseRooms_titleBox"
        style="height:3rem;"
      >
        <div class="chooseRooms_top">设置租金</div>
        <div class="chooseRooms_bottom">已选择{{ chooseRoomsSetDeposit.length }}个房间：{{ roomSns }}</div>
      </div>
      <mt-field
        label="租金"
        placeholder="请输入租金"
        type='number'
        v-model="roomAmount"
        class="inp_field"
      >/月</mt-field>
      <mt-field
        label="押金"
        placeholder="请输入押金"
        type='number'
        v-model="deposit"
        class="inp_field"
      ></mt-field>
      <p
        class="chooseRooms_set_ok"
        @click='setDepositOk'
      >确定</p>
    </mt-popup>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';
import { GetBusinessRoomFreeMonthByhotel } from '@/api/api_business';
import { Dialog } from 'vant';

export default {
    name: 'add',
    props: {
        roomList: Array
    },
    data() {
        return {
            allConcatRooms: [],
            text: '',
            roomSnList: [],
            showRooms: false,
            showDeposit: false,
            chooseRoomsSetDeposit: [],
            roomSns: '',
            dataForm: {
                roomAmount: null,
                deposit: null
            },
            roomAmount: null,
            deposit: null
        };
    },
    watch: {
        roomList: {
            handler: function (val) {
                this.allConcatRooms = val;
            },
            deep: true
        }
    },
    created() {
        this.dataForm.hotelId = this.$route.query.hotelId;
        this.getBusinessOrder(this.dataForm.hotelId);
    },
    computed: {
        compputerWidth() {
            return window.innerWidth / 3.5 + 'px';
        },
        roomSnListType: {
            get() {
                return this.roomSnList.filter((item) => item.itemType ==
 true);
            }
        }
    },
    methods: {
        _next() {
            this.$emit('callback', this.allConcatRooms);
        },
        getBusinessOrder(hotelId) {
            GetBusinessRoomFreeMonthByhotel({ hotelId: hotelId }).done((res) => {
                res.data.forEach((element) => {
                    element.imgStatu = false;
                    element.itemType = true;
                });
                this.roomSnList = res.data;
                this.hotelNum = res.other;
                this.roomSnOrder = [];
                res.order.map((item) => {
                    let obj = {};
                    obj.roomScaleId = item.id;
                    obj.scaleName = item.scaleName;
                    obj.freeNum = item.freeNum;
                    this.roomSnOrder.push(obj);
                });
                for (let i = 0; i < this.roomSnList.length; i++) {
                    for (let j = 0; j < this.roomSnOrder.length; j++) {
                        if (this.roomSnList[i].roomScaleId == this.roomSnOrder[j].roomScaleId) {
                            this.roomSnList[i].freeNum = this.roomSnOrder[j].freeNum;
                        }
                    }
                }
            });
        },
        // 选择房间号
        choose_rooms(room) {
            room.imgStatu = !room.imgStatu;
        },
        // 设置押金
        setDeposit() {
            this.chooseRoomsSetDeposit = this.roomSnList.filter((item) => item.itemType == true && item.imgStatu == true);
            if (this.chooseRoomsSetDeposit.length == 0) {
                Toast({
                    message: '请选择房间号',
                    duration: 1000
                });
                return;
            }
            let _list = this.chooseRoomsSetDeposit.map((item) => item.roomSn).join('、');
            this.roomSns = _list;
            this.showRooms = false;
            this.showDeposit = true;
        },
        // 设置押金-ok
        setDepositOk() {
            if (!this.roomAmount) {
                Toast({
                    message: '请输入租金',
                    duration: 1000
                });
                return false;
            }
            if (!this.deposit) {
                Toast({
                    message: '请输入押金',
                    duration: 1000
                });
                return false;
            }
            for (let i = 0; i < this.roomSnOrder.length; i++) {
                let item = this.roomSnOrder[i];
                let _clist = this.chooseRoomsSetDeposit.filter((itm) => itm.roomScaleId == item.roomScaleId);
                let num = _clist.length;
                if (num > item.freeNum) {
                    let _str = _clist.map((i) => i.roomSn).join(',');
                    Dialog({ message: `以下房间${_str},最多可选择${item.freeNum}间` }).then(() => {
                        this.showRooms = true;
                        this.showDeposit = false;
                    });
                    return;
                }
            }
            this.chooseRoomsSetDeposit.map((item) => {
                item.roomAmount = this.roomAmount;
                item.itemType = false;
                item.deposit = this.deposit;
                return item;
            });
            this.allConcatRooms = [...this.allConcatRooms, ...this.chooseRoomsSetDeposit];
            this.chooseRoomsSetDeposit = [];
            this.showDeposit = false;
        },
        // 删除房间
        deleteRoom(item) {
            this.allConcatRooms = this.allConcatRooms.filter((room) => room.id != item.id);
            item.imgStatu = false;
            let index = this.roomSnList.findIndex((itm) => itm.id ==
 item.id);
            this.roomSnList[index].itemType = true;
            this.roomSnList[index].imgStatu = false;
        }
    }
};
</script>
<style scoped>
#rooms {
    height: 100%;
}
.rooms_box {
    display: flex;
    display: -webkit-flex;
    justify-content: start;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    margin: 20px 10px;
}
.choose_room {
    border: 1px solid #e5e5e5;
    background: #fff;
    height: 83px;
    position: relative;
    text-align: left;
    padding: 6px 4px;
    box-sizing: border-box;
    color: #6a6a6a;
    font-size: 0.7rem;
    line-height: 20px;
    margin-bottom: 10px;
    width: 30%;
    margin-right: 2.5%;
}
.room_sn {
    font-size: 0.9rem;
    font-weight: bold;
    color: #000;
    margin-bottom: 8px;
}
.room_img {
    position: absolute;
    font-size: 1rem;
    color: #ff5257;
    top: -8px;
    right: -8px;
    width: 16px;
}
/* 添加房间号 */
.addRooms {
    width: 95%;
    background: #fff;
    color: #000;
    margin: 20px auto;
}
/* 弹出框-选择房间号 */
.chooseRooms {
    width: 100%;
    height: 80%;
    font-size: 0.9rem;
    text-align: left;
}
.chooseRooms_titleBox {
    padding: 10px 16px;
    color: #6a6a6a;
    font-size: 0.6rem;
    height: 2rem;
}
.chooseRooms_top {
    font-weight: bold;
    color: #000;
    font-size: 0.8rem;
}
.chooseRooms_box {
    border-bottom: 1px solid #f4f4f4;
    display: flex;
    padding: 16px;
    align-items: center;
}
.chooseRooms_img {
    width: 16px;
    height: 16px;
    margin-right: 20px;
}
.chooseRooms_label {
    width: 16px;
    height: 16px;
    border-radius: 10px;
    border: 1px solid #bababa;
    margin-right: 20px;
}
.inp_field {
    padding: 0 6px;
    color: #000;
}
.chooseRooms_set {
    height: calc(100% - 80% - 2rem - 24px);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #ff7d41;
    border-top: 4px solid #f4f4f4;
    text-align: center;
    font-size: 0.8rem;
}
/deep/ .mint-cell-wrapper {
    font-size: 0.8rem;
}
/deep/ .mint-field-core {
    background: #fafafa;
}
/deep/ .mint-cell-value {
    background: #fafafa;
}
/* 弹出框-押金 */
.setD {
    height: auto;
}
.chooseRooms_set_ok {
    height: 2rem;
    border-top: 4px solid #f4f4f4;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.8rem;
}
.mintui-more:before {
    color: #fff;
}
.mintui {
    color: #fff;
}
.mint-header {
    color: #fff;
    height: 8%;
    background-color: #393939;
}
</style>



