<template>
  <div class="group">
    <van-form @submit="onSubmit">
      <van-field :value="$route.query.cooperationName"
                 label="单位名称"
                 input-align="right"
                 readonly
                 right-icon="arrow" />

      <van-field :value="dataForm.roomNum"
                 @click="showSelectRoom = true"
                 label="选择房间"
                 input-align="right"
                 readonly
                 right-icon="arrow" />

      <van-field style="margin-top: 18px"
                 :value="dataForm.startTime"
                 @click="showStartTime = true"
                 label="开始时间"
                 input-align="right"
                 right-icon="arrow"
                 readonly />
      <van-field clickable
                 readonly
                 input-align="right"
                 v-model="longTime"
                 label="租期设置"
                 right-icon="arrow"
                 placeholder="请选择租期设置"
                 @click="showPicker = true" />
      <van-field clickable
                 readonly
                 input-align="right"
                 name="paymentMethod"
                 v-model="paymentMethod"
                 label="缴费周期"
                 right-icon="arrow"
                 placeholder="请选择结账周期"
                 @click="showPickerPayment = true" />

      <van-field style="margin-top: 12px"
                 v-model="dataForm.roomUser"
                 name="roomUser"
                 label="联系人"
                 placeholder="请输入联系人"
                 :rules="[{ required: true, message: '请输入联系人' }]" />
      <van-field v-model="dataForm.phone"
                 name="phone"
                 type="Number"
                 label="联系方式"
                 placeholder="请输入联系方式"
                 :rules="[{ required: true, message: '请输入联系方式' }]" />

      <div style="margin: 16px">
        <van-button class="submit-btn"
                    block
                    native-type="submit"> 确认创建 </van-button>
      </div>
    </van-form>

    <van-popup v-model="showPicker"
               position="bottom">
      <van-picker show-toolbar
                  :columns="columnsPeriod"
                  @confirm="onConfirm"
                  @cancel="showPicker = false" />
    </van-popup>

    <van-popup v-model="showPickerPayment"
               position="bottom">
      <van-picker show-toolbar
                  :columns="columnsPayment"
                  @confirm="onConfirmPayment"
                  @cancel="showPickerPayment = false" />
    </van-popup>

    <div v-show="showSelectRoom"
         class="all-rooms-box">
      <all-rooms @cancel="showSelectRoom = false"
                 @room="roomList"
                 @callback="_callback"></all-rooms>
    </div>
    <van-calendar v-model="showStartTime"
                  :min-date="minDate"
                  :max-date="maxDate"
                  @confirm="onConfirmStartTime" />
  </div>
</template>

<script>
import AllRooms from './allRooms';
import { GetSystemDictDataByType } from '@/api/api_query';
import { PostBusinessCooperOrderAdd } from '@/api/api_business';
import dayjs from 'dayjs';
import Vue from 'vue';
import { Calendar, Toast } from 'vant';
Vue.use(Toast);
export default {
    name: 'add',
    components: { AllRooms },
    data() {
        return {
            showPicker: false,
            showPickerPayment: false,
            showSelectRoom: false,
            roomList: [],
            longTime: null, //租期时长
            dataForm: {
                startTime: dayjs(new Date()).format('YYYY-MM-DD'),
                hotelId: null,
                cooperationId: null,
                roomNum: 0,
                diffDay: null,
                paymentMethod: null,
                roomInfo: null,
                phone: '',
                roomUser: ''
            },
            paymentMethod: null,
            columns: ['1个月', '3个月', '6个月', '12个月', '24个月'],
            columnsPeriod: [],
            columnsPayment: [],
            columnsPaymentAll: [],
            showStartTime: false,
            minDate: null,
            maxDate: new Date()
        };
    },
    created() {
        this.$store.commit('setHeader', '新增单位订单');
        this.dataForm.hotelId = this.$route.query.hotelId;
        this.dataForm.cooperationId = this.$route.query.cooperationId;
        this.dataForm.roomUser = this.$route.query.contact;
        this.dataForm.phone = this.$route.query.phone;
        this.minDate = new Date(dayjs(this.maxDate).subtract(1, 'month'));
        this.getTypeBusinessBill();
    },
    watch: {
        longTime(neww, oldd) {
            let c_dictValue = this.columnsPeriodAll.find((item) => item.dictLabel == neww).dictValue;
            if (this.columnsPaymentAll && this.columnsPaymentAll.length) {
                this.columnsPayment = this.columnsPaymentAll.filter((item) => Number(c_dictValue) >= Number(item.dictValue)).map((item) => item.dictLabel);
                this.paymentMethod = this.columnsPayment[0];
            }
            let startTime = dayjs().subtract(c_dictValue, 'month');
            let diffDay = dayjs(dayjs()).diff(dayjs(startTime), 'days');
            this.dataForm.diffDay = diffDay;
        }
    },
    methods: {
        //选择房间
        _callback(val) {
            this.showSelectRoom = false;
            this.roomList = val;
            this.dataForm.roomNum = val.length;
        },

        //查询字典 -- 查询缴费周期
        getTypeBusinessBill() {
            GetSystemDictDataByType({ dictType: 'business_payment_method' }).done((res) => {
                this.columnsPaymentAll = res.data;
            });
            GetSystemDictDataByType({ dictType: 'business_contract_period' }).done((res) => {
                this.longTime = res.data[0].dictLabel;
                this.columnsPeriod = res.data.map((item) => item.dictLabel);
                this.columnsPeriodAll = res.data;
            });
        },
        // 选择开始时间-1
        formatDate(date) {
            let y = date.getFullYear();
            let m = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1;
            let d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
            this.startTime = y + '-' + m + '-' + d;
            return `${date.getFullYear()}-${m}-${d}`;
        },
        // 选择开始时间-2
        onConfirmStartTime(date) {
            this.showStartTime = false;
            this.dataForm.startTime = this.formatDate(date);
        },

        //提交表单
        onSubmit() {
            if (this.dataForm.roomNum == 0) {
                Toast({
                    message: '请选择房间',
                    duration: 1000
                });
                return false;
            }
            let roomInfo = this.roomList.map((item) => {
                let obj = {
                    roomId: item.id,
                    roomScaleId: item.roomScaleId,
                    roomAmount: item.roomAmount,
                    deposit: item.deposit
                };
                return obj;
            });
            this.dataForm.paymentMethod = this.columnsPaymentAll.find((item) => item.dictLabel == this.paymentMethod).dictValue;
            this.dataForm.roomInfo = JSON.stringify(roomInfo);
            this.dataForm.startTime = this.startTime ? this.startTime : dayjs(this.dataForm.startTime).format('YYYY-MM-DD');
            PostBusinessCooperOrderAdd(this.dataForm).done(() => {
                Toast.success('操作成功');
                setTimeout(() => {
                    this.$router.go(-1);
                }, 2000);
            });
        },
        //确认选择租期时长
        onConfirm(val) {
            this.longTime = val;
            this.showPicker = false;
            return;
        },
        onConfirmPayment(val) {
            this.paymentMethod = val;
            this.showPickerPayment = false;
            return;
        }
    }
};
</script>

<style scoped>
.group {
    height: calc(100vh - 8%);
    overflow-y: scroll;
}
.submit-btn {
    background: #ff7d41;
    color: #fff;
}
.all-rooms-box {
    position: fixed;
    z-index: 2;
    width: 100vw;
    height: 100vh;
    top: 0;
    background: #efeff4;
    transition: 1s;
}
/deep/ .van-field__label {
    text-align: left;
}
</style>



